<template>
    <v-app id="app">
        <router-view name="header" @transferParamType="getTransferParamType" @transferParamTag="getTransferParamTag"/>
        <v-content>
            <!--name省略表示name='default'-->
            <v-fade-transition>
                <keep-alive exclude="ArticleDetail">
                    <router-view :param-type="paramType" :param-tag="paramTag"/>
                </keep-alive>
            </v-fade-transition>
        </v-content>
        <router-view name="footer"/>
    </v-app>
</template>

<script>
    export default {
        name: 'App',
        components: {},
        methods: {
            getTransferParamType(paramType) {
                this.paramType = paramType
            },
            getTransferParamTag(paramTag) {
                this.paramTag = paramTag
            }
        },
        props: {
            attrs: {
                type: Object,
                default: () => ({}),
            },
        },
        data: vm => ({
            initialDark: vm.$vuetify
                ? vm.$vuetify.theme.dark
                : false,
            paramType: String,
            paramTag: String
        }),
        beforeDestroy() {
            if (!this.$vuetify) return
            this.$vuetify.theme.dark = this.initialDark
        },
        created() {
            document.title = "平凡之路的个人博客"
        }
    };
</script>
